body{
    margin: 0px;
}

.border{
    border: 2px solid red;
}

.wrapper{
  display: flex;
  height: 98vh;
  flex-direction: column;  
  border: 2px solid;
  margin-top: 5px;
}

.width-50{
    width: 50%;
    height: 50vh;
}

.btn{
  padding-top: 11px;
  padding-bottom: 10px;
  padding-left: 20px;
  padding-right: 20px;
  margin-right: 10px;
  background-color: #1976d2;
  color: white;
  border: 2px solid;
  margin: 15px;
}

.login-form{
    display: flex;
    flex-direction: column;
    align-items: center;
    font-size: 20px;
    margin-top: 15px;
  }
  
  .label{
    display: inline-block;
      width: 150px;
  }
  
  .input{
    width: 250px;
      font-size: 18px;
  }
  
  .m-top-15{
    margin-top: 15px;
  }

  .m-left-10{
    margin-left: 10px;
  }


  .err-msg{
    color: red;
  }

  .header{
    border: 2px solid black;
    height: 8%;
    display: flex;
    flex-direction: row;
    align-items: center;
  }

  .search-box{
    font-size: 24px;
    width: 300px;
    height: 25px;
  }

  .body{
    border: 2px solid blue;
    height: 90%;
    display: flex;
    flex-direction: row;
  }

  /* .sidebar{
    border: 2px solid red;
    width: 20%;
    display: flex;
    flex-direction: column;
    align-items: center;
  } */

  .content{
    border: 2px solid green;
    width: 80%;
  }

  .sidebar{
    border: 2px solid red;
    width: 20%;
    display: flex;
    flex-direction: column;
    align-items: center;
  }
  

  .active-btn{
    background-color: black;
    border: 2px solid black;
  }